<template>
  <div>
      <div class="title">
          员工管理
      </div>
      <!-- 这是关联用户 -->
      <div class="relationBox_b">
          <div class="jgTab">
            <!-- 这是绑定的人员数据表 -->
            <div class="tablebox">
              <div class="jieGou_tit">
                员工列表
                <div class="jieGou_tit_rigth">
                  <el-button size="small" @click="Fnstaff()">新增员工</el-button>
                </div>
                <el-divider></el-divider>
              </div>
              <el-table
                :data="tableDatareRE"
                stripe
                style="width: 100%">
                <el-table-column
                  prop="username"
                  label="用户名"
                  width="180">
                </el-table-column>
                <el-table-column
                  :filters="[{ text: '客服', value: '客服' }, { text: '运维', value: '运维' }, { text: '运营', value: '运营' }]"
                  :filter-method="filterTag"
                  filter-placement="bottom-end"
                  label="部门">
                    <template slot-scope="scope">
                        {{scope.row.u_type}}
                    </template>
                </el-table-column>
                <el-table-column
                  prop="phone_numbers"
                  label="手机">
                </el-table-column>
                <el-table-column
                  width="180"
                  label="操作">
                  <template slot-scope="scope">
                      <!-- scope.row.name -->
                      <el-button size="small" type="warning" @click.native="Fnrevise(scope.row)">编辑</el-button>
                      <el-button @click.native="Fnxx(scope.row.user_id)" size="small" type="danger">停用</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
      </div>
      <!-- 这是编辑弹出页面start -->
      <!-- 新增用户start-->
      <el-dialog :title="revise ? '修改' : '新增'" :visible.sync="dialogFormVisible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="用户名" prop="staff_name">
            <el-input v-model="ruleForm.staff_name"></el-input>
          </el-form-item>
          <el-form-item label="部门" prop="u_type">
            <el-select v-model="ruleForm.u_type" placeholder="请选择部门">
              <el-option label="运维" value="运维"></el-option>
              <el-option label="客服" value="客服"></el-option>
              <el-option label="运营" value="运营"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="手机号" prop="staff_phone">
            <el-input v-model="ruleForm.staff_phone"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="staff_password">
            <el-input v-model="ruleForm.staff_password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">{{revise ? '修改' : '新增'}}</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!-- 新增用户end-->

  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
    data(){
        return{
            tableDatareRE:[{
                date: '这是家',
                name: '家',
                address: '上海市普陀区金沙江路 1518 弄'
              }, {
                date: '2016-05-04',
                name: '公司',
                address: '上海市普陀区金沙江路 1517 弄'
              }, {
                date: '这是公司',
                name: '公司',
                address: '上海市普陀区金沙江路 1519 弄'
              }, {
                date: '2016-05-03',
                name: '家',
                address: '上海市普陀区金沙江路 1516 弄'
            }],//这是绑定的人员数据表
            dialogFormVisible: false,//表单打开新增用户
            ruleForm: {
              user_id: 0,
              staff_name: "",
              staff_phone: "",
              staff_password: "",
              u_type: ""
            },
            rules: {
              staff_name: [
                { required: true, message: '请输入用户名', trigger: 'blur' },
              ],
              staff_phone: [
                { required: true,pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/, message: '请输入正确的手机号', trigger: 'blur' }
              ],
              staff_password: [
                { required: true, pattern:/^(\w){6,20}$/,message: '请输入密码', trigger: 'blur' }
              ],
              u_type: [
                { required: true,message: '请选择部门', trigger: 'blur' }
              ],
            },
            revise:false,//是否是修改
        };
    },
    methods:{
      submitForm(formName) {//点击创建按钮=-==用户
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // 添加
              this.ruleForm.user_id=this.buserInfo.user_id
            if(!this.revise){
              url.managercreatestaffv2(this.ruleForm)
              .then(res=>{
                if(res.data.code[0]="200"){
                  this.dialogFormVisible=false;//关闭弹窗
                  this.Fnemployeelist()//后台人员列表
                  this.$message.success(res.data.message[0]);
                }else{
                  this.$message.error(res.data.message[0]);
                }
              })
            }else{
              // 修改
              url.editstaff(this.ruleForm)
              .then(res=>{
                console.log(res,'----------修改员工')
                if(res.data.code[0]="200"){
                  this.dialogFormVisible=false;//关闭弹窗
                  this.Fnemployeelist()//后台人员列表
                  this.$message.success(res.data.message[0]);
                }else{
                  this.$message.error(res.data.message[0]);
                }
              })
            }

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      filterTag(value, row) {//row.name---其中name和prop绑定的名字相同
        return row.u_type === value;
      },
      Fnxx(user_id){//停用操作
        url.deactivatestaffv2({
          "user_id": user_id,
          "user_id1": this.buserInfo.user_id,
        }).then(res=>{
          if(res.data.code[0]='200'){
            this.$message.success(res.data.message[0]);
            this.Fnemployeelist()//调用员工列表
          }else{
            this.$message.error(res.data.message[0]);
          }
        })
      },
      Fnemployeelist(){//员工列表
        url.employeelist({
          user_id:this.buserInfo.user_id
        }).then(res=>{
          console.log(res,'---------后台人员列表')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.user_id=element.pk
          newlist.push(element.fields)
          });
          this.tableDatareRE=newlist
        })
      },
      Fnrevise(row){//修改按钮
        console.log(row,'-----------修改传递的参数')
        this.dialogFormVisible = true;
        this.revise=true
        this.ruleForm={
          user_id1:row.user_id,
          staff_name: row.username,
          staff_phone: row.phone_numbers,
          staff_password: "",
          u_type: row.u_type
        }
      },
      Fnstaff(){//新增按钮
        this.dialogFormVisible = true
        this.revise=false
      }
    },
    computed:{
      ...mapState(['buserInfo'])
    },
    mounted(){
      this.Fnemployeelist()//后台人员列表
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.relationBox_b
  padding 30px
  .jgTab
    display flex
    .jieGou
      width 300px
    .jieGou_tit
      margin-bottom 17px
      .jieGou_tit_rigth
        width 120px
        float right
    .tablebox
      flex 1
      margin-left 30px
</style>


